.transition {
  background-color: #F5F6F6;
  padding: 144px 0;
}
.transition .block-title {
  max-width: 440px;
  margin-bottom: 72px;
}
.transition .items {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -16px;
}
.transition .items__item {
  width: calc((100% / 3) - 32px);
  margin: 0 16px;
  background-color: #fff;
  border-radius: 16px;
  padding: 50px 32px;
}
.transition .items__item.blue {
  background-color: #115CEF;
  position: relative;
  overflow: hidden;
}
.transition .items__item.blue .number {
  color: #fff;
}
.transition .items__item.blue .number:before {
  color: #fff;
}
.transition .items__item.blue .name {
  color: #fff;
}
.transition .items__item.blue .bg {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.transition .items__item.blue .bg .line img {
  width: 100%;
  display: block;
}
.transition .items__item.blue .bg .star {
  position: absolute;
}
.transition .items__item.blue .bg .star:nth-of-type(1) {
  top: 17px;
  right: 94px;
}
.transition .items__item.blue .bg .star:nth-of-type(2) {
  top: 33px;
  right: 56px;
}
.transition .items__item.blue .bg .star:nth-of-type(3) {
  top: 57px;
  right: 75px;
}
.transition .items__item .number {
  color: #115CEF;
  font-size: 60px;
  line-height: 1.2;
  letter-spacing: -0.5px;
  font-weight: 700;
  position: relative;
  z-index: 1;
  padding-left: 7px;
  margin-bottom: 28px;
}
.transition .items__item .number::before {
  content: attr(data-number);
  position: absolute;
  top: -6px;
  left: -2px;
  font-size: 60px;
  line-height: 1.2;
  letter-spacing: -0.5px;
  font-weight: 700;
  color: #115CEF;
  opacity: 0.1;
  z-index: -1;
}
.transition .items__item .name {
  font-size: 24px;
  font-weight: 700;
  line-height: 1.3;
  letter-spacing: 0.18px;
  margin: 0 0 16px;
}
.transition .items__item p {
  font-size: 20px;
  line-height: 1.6;
  letter-spacing: 0.15px;
  font-weight: 500;
  margin: 0;
  color: #76767B;
}
